.container {
    grid-area: rotateloader;
    margin: 0;
    padding: 0;
    background: #000;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.colors {
    position: relative;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    animation: rotate 2s linear infinite;
}

.circle{
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background: linear-gradient(to top, transparent,transparent 40%,#e5f403);
    animation: loader 4s linear infinite;
}

.circle:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    border-radius: 50%;
    background: linear-gradient(to top, transparent,transparent 40%,#e5f403);
    filter: blur(15px);
    z-index: 1000;
    animation: loaderbefore 4s linear infinite;
}

.circle:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    border-radius: 50%;
    background: linear-gradient(to top,transparent,transparent 40%, #e5f403);
    filter: blur(50px);
    z-index: 1;
    animation: loaderafter 4s linear infinite;
}

.block {
    position: absolute;
    top: 7px;
    left: 7px;
    bottom: 7px;
    right: 7px;
    border-radius: 50%;
    background: radial-gradient(#000, #000,#000, rgba(0, 0, 0, 0));
    z-index: 10;
    box-shadow: inset 0 15px 15px rgba(255, 255, 255, 0.12);
}


@keyframes loader {
    0% {
        filter: hue-rotate(0);
    }
    100% {
        filter: hue-rotate(360deg);
    }
}

@keyframes loaderbefore {
    0% {
        filter: hue-rotate(0) blur(20px);
    }
    100% {
        filter: hue-rotate(360deg) blur(20px);
    }
}

@keyframes loaderafter {
    0% {
        filter: hue-rotate(0) blur(5px);
    }
    100% {
        filter: hue-rotate(360deg) blur(5px);
    }
}

@keyframes rotate {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(360deg);
    }
}


